
.ImageGalleryList{
    display: grid;
    max-width: calc(100vw - 48px);
    grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
    grid-gap: 16px;
    margin-top: 0;
    margin-bottom: 0;
    padding: 0;
    list-style: none;
    margin-left: auto;
    margin-right: auto;
}

.preTitle{
display: inline-block;
font-size: 30px;
font-family: sans-serif;
margin-top: 30px;

}
